	module.exports = {"data":{"challengeNode":{"title":"Build a Voting App","description":["<strong>目标：</strong>开发一个功能类似于 <a href='https://fcc-voting-arthow4n.herokuapp.com/' target='_blank'>https://fcc-voting-arthow4n.herokuapp.com/</a>这个示例的全栈式 JavaScript 应用，并将其部署到 Heroku 上。","你应该为每一个项目创建一个 GitHub 仓库以及 Heroku 项目。如果你不记得如何操作，请查看<a href='/challenges/get-set-for-our-dynamic-web-application-projects'>https://freecodecamp.com/challenges/get-set-for-our-dynamic-web-application-projects</a>.","下面是你应该在这个项目完成的需求：","<strong>需求：</strong>作为一个已授权的用户，我可以保持我的投票，并可以在稍后访问它们。","<strong>需求：</strong>作为一个已授权的用户，我可以分享我的投票给好友。","<strong>需求：</strong>作为一个已授权的用户，我可以看到我投票的汇总结果。","<strong>需求：</strong>作为一个已授权的用户，我可以删除决定不想要的投票内容。","<strong>需求：</strong> 作为一个已授权的用户，我可以创建一个包含任意数量选项的投票。","<strong>需求：</strong>无论是否授权，我都可以查看投票并参与任何人的投票。","<strong>需求：</strong>无论是否授权，我都可以在图表中查看投票结果。（可以通过使用 Chart.js 或 Google Charts 实现）","<strong>需求：</strong>作为一个已授权的用户，如果我不喜欢投票中的选项，我可以自己创建一个新的选项。","当你完成以上需求,点击\"我已经完成了这个挑战\"按钮并输入你的 GitHub 仓库地址和 Heroku 地址。","你可以通过将你的项目分享到社交网络来获得反馈。"],"challengeType":4,"fields":{"blockName":"Take Home Projects","slug":"/coding-interview-prep/take-home-projects/build-a-voting-app"}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/take-home-projects/build-a-pong-game","id":"bd7158d8c443eddfaeb5bdef"},"slug":"/coding-interview-prep/take-home-projects/build-a-voting-app"}}

